
@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@100&family=Poppins:ital,wght@0,400;1,200;1,300&display=swap');
*{
    margin: 0;
    box-sizing: border-box;
    &::after,
    &::before{
        box-sizing: border-box;
    }
    font-weight: 800;
  
}
html,body{
    overflow-x: hidden;
}

body{
    font-family: 'Montserrat', sans-serif;
    line-height: 1.5;
    -webkit-font-smoothing:antialiased
    
}

button,
input,
textarea,
select{
    font: inherit;
}

p,h1,h2,h3,h4,h5,h6{
    overflow-wrap: break-word;
}

img,
video,
canvas,
svg{
    max-width: 100%;
}

a{
    text-decoration: none;
    color: inherit;
}

ul{
    list-style: none;

}